import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { ModalController } from '@ionic/angular';
import { EMPTY, from } from 'rxjs';
import { catchError, concatMap, first } from 'rxjs/operators';

import { ApiClientModule, ApiRequest, TagModel, TagStatus } from '../include/api-client/api-client.module';
import { AppLogService } from '../include/app-log/app-log.service';
import { DefaultCoverPhoto } from '../include/app-settings/app-settings.module';
import { LocationService } from '../include/location/location.service';
import { Translations } from '../include/i18n/i18n.module';

import { CreateDialogPage, CssClass as CreateDialogCssClass } from '../create-dialog/create-dialog.page';
import { EmptyTagDialogPage, CssClass as EmptyTagDialogCssClass } from '../empty-tag-dialog/empty-tag-dialog.page';

@Component({
  selector: 'app-discover',
  templateUrl: './discover.page.html',
  styleUrls: ['./discover.page.scss'],
})
export class DiscoverPage implements OnInit {

    constructor(
        private route: ActivatedRoute,
        private api: ApiClientModule,
        private log: AppLogService,
        private gps: LocationService,
        private dialog: ModalController,
        private nav: Router,
        private translate: Translations,
    ) { }
    
    hasContent: boolean;
    title: string;
    coverPhoto: string = DefaultCoverPhoto;
    
    tagHandle: string
    arkHandle: string

    async ngOnInit() {
		this.tagHandle = this.route.snapshot.paramMap.get('tag');
    }

    async ionViewWillEnter() {
		if (this.tagHandle && this.tagHandle.length > 0) {
		    await this.readTag();
		} else {
            this.nav.navigate(['/']);
        }
    }
    
    private async readTag() {
        let pos = undefined;

        try {
            pos = await this.gps.getLocation();
        } catch {}

        let point = pos ? `${pos.lat},${pos.lon}` : '';
        let req = new ApiRequest(`/tag/${this.tagHandle}`)
            .addHeaders([ { name: 'x-hushtags-tapLocation', value: point } ]);

        this.api.get<TagModel>(req)
            .subscribe( async res => {
                this.title = res.title;
                this.arkHandle = res.contents.length == 1 ? res.contents[0].handle : '';
                this.hasContent = res.contents.length > 0;

                if (res.status === TagStatus.Unassigned) {
                    let msg = await this.dialog.create({
                        component: CreateDialogPage,
                        cssClass: CreateDialogCssClass,
                    });

                    msg.onDidDismiss().then( () => this.nav.navigate(['/']));

                    await msg.present();
                } else if (!this.hasContent) {
                    let msg = await this.dialog.create({
                        component: EmptyTagDialogPage,
                        cssClass: EmptyTagDialogCssClass,
                    });

                    msg.onDidDismiss().then( () => this.nav.navigate(['/']));

                    await msg.present();
                }

                from(res.contents)
                    .pipe(
                        concatMap( ark =>
                            this.api.get(new ApiRequest(`/ark/${this.tagHandle}/${ark.handle}`)).pipe( catchError( () => EMPTY ) )
                        , ark => ark ),
                        first()
                    )
                    .subscribe(
                        ark => {
                            let img = new Image();
                            let coverPhoto = this.api.url(`/ark/${this.tagHandle}/${ark.handle}/_cover`);

                            img.onload = () => this.coverPhoto = coverPhoto;
                            img.src = coverPhoto;
                        },

                        err => {
                            this.log.ex("got error", err);
                        }
                    );
            });
    }
}
